class Youtube {
    constructor(key) {
        this.key = key;
        this.getRequestOptions = {
            method: 'GET',
            redirect: 'follow'
          };
    }

    async mostPopular() {
        const response = await fetch(
            `https://youtube.googleapis.com/youtube/v3/videos?part=snippet&chart=mostPopular&maxResults=25&regionCode=KR&key=${this.key}`,
            this.getRequestOptions
        );
        const result_1 = await response.json();
        return result_1.items;
    }

    async search(query) {
        const response = await fetch(
            `https://youtube.googleapis.com/youtube/v3/search/?part=snippet&maxResults=25&q=${query}&type=video&key=${this.key}`,
            this.getRequestOptions
        );
        const result_1 = await response.json();
        return result_1.items.map(item => ({ ...item, id: item.id.videoId }));
    }

    async Chnnels(channelId) {
        const response = await fetch(
            `https://youtube.googleapis.com/youtube/v3/channels?part=snippet, contentDetails, statistics&id=${channelId}&key=${this.key}`,
            this.getRequestOptions
        );
        const result_1 = await response.json();
        return result_1.items;
    }

    async Comment(videoId) {
        const response = await fetch(
            `https://youtube.googleapis.com/youtube/v3/commentThreads?part=snippet, replies&order=relevance&videoId=${videoId}&key=${this.key}`, 
            this.getRequestOptions
        );
        const result_1 = await response.json();
        return result_1.items;
    }
}

export default Youtube;